import { Injectable } from '@angular/core';
import { AuthService } from '../../common/services/permission/authService.service';
import { GlobalState } from '../../global.state';
import { Employee, ScheduleInterface, Service, Room, Customer } from '../../common/interfaces/index';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Subject } from 'rxjs';
@Injectable()
export class ScheduleInputService {
    private _scheduleList: any[] = [{}];
    constructor(private http: HttpClient, private authService: AuthService, private state: GlobalState) { }
    private get apiUrl(): string {
        let url = this.state.getGlobalSetting("apiUrl");
        let apiUrl = "";

        if (!!url && url.length > 0) { apiUrl = url[0] };
        return apiUrl
    }
    private getRequestOption() {
        let headers: HttpHeaders = new HttpHeaders({ 'Content-type': 'application/json', 'Authorization': this.authService.getAuth().token })
        return { headers: headers };
    }

    public getUser() {
        return this.authService.getUserProfile()
    }

    public getEmployeeSchedule(param: any) {
        let returnSubject: Subject<any> = new Subject();
        this.http.post(this.apiUrl + '/getEmpScheduleList', param, this.getRequestOption())
        .subscribe((data) => {
                returnSubject.next(data);
                returnSubject.unsubscribe();
            });
        return returnSubject;
    }

    public getRoomSchedule(param: any) {
        let returnSubject: Subject<any> = new Subject();
        this.http.post(this.apiUrl + '/getRoomScheduleList', param, this.getRequestOption())
        .subscribe((data) => {
                returnSubject.next(data);
                returnSubject.unsubscribe();
            });
        return returnSubject;
    }

    public getCustomerSchedule(param: any) {
        return this.http.post(this.apiUrl + '/GetCustomerSchedule', param, this.getRequestOption())
    }

    public getGUID() {
        return this.http.get(this.apiUrl + '/getGuid', this.getRequestOption());
    }

    public checkHoliday(param){
        return this.http.post(this.apiUrl + '/checkHoliday', param, this.getRequestOption())       
    }

    public getTimeSlotForService(outletId: string, serviceId: number, appointmentDate: Date) {
        let param = {
            outletId: outletId,
            serviceId: serviceId,
            appointmentDate: appointmentDate
        };
        return this.http.post(this.apiUrl + '/getTimeSlotForService', param, this.getRequestOption())
    }
   
}