import { Injectable } from '@angular/core';

export interface BranchSettings {
  enableCheckAll: boolean;
  text: string;
  enableSearchFilter: boolean;
  labelKey: string;
  primaryKey: string;
  badgeShowLimit: number;
}

@Injectable({
  providedIn: 'root'
})
export class BranchSettingService {

  constructor() {}

  getBranchSettings(): any {
    return {
      enableCheckAll: true,
      text: 'Select Outlets',
      enableSearchFilter: true,
      labelKey: 'branchName',
      primaryKey: 'branchId',
      badgeShowLimit: 2
    };
  }
}
