import { Component, HostListener, Output, EventEmitter } from '@angular/core';
import { TimelineModel } from 'src/app/common/interfaces';
import { AuthService } from 'src/app/common/services/permission/authService.service';

@Component({
  selector: 'timeline-context-menu',
  templateUrl: './timelineContextMenu.component.html',
  styleUrls: ['./timelineContextMenu.component.css']
})
export class TimelineContextMenuComponent {
  @Output() onAction = new EventEmitter<any>();
  contextMenuVisible = false;
  menuLeft = 0;
  menuTop = 0;
  model :TimelineModel;

  constructor(private authService: AuthService){}

  get canCheckIn() {
    const date = new Date();
    const appDate = new Date(this.model.startDate);
    const dateOnly = new Date(date.getFullYear(), date.getMonth(), date.getDate());
    const appDateOnly = new Date(appDate.getFullYear(), appDate.getMonth(), appDate.getDate());
    const AllowCheckInBefore = this.authService.getSetting().AllowCheckInBefore;
    return dateOnly.getTime() == appDateOnly.getTime() && (appDate.getTime() - (AllowCheckInBefore * 60 * 1000)) < new Date().getTime() && this.model.status == 0;
  }


  public showContextMenu(event: MouseEvent, model: TimelineModel) {
    event.preventDefault();
    this.contextMenuVisible = true;
    this.menuLeft = event.clientX;
    this.menuTop = event.clientY;
    this.model = model;
    console.log(event);
  }

  onMenuAction(action: string) {
    console.log('Selected action:', action);
    this.hideContextMenu();
    this.onAction.emit({action,model:this.model});
  }

  hideContextMenu() {
    this.contextMenuVisible = false;
  }

  @HostListener('document:click')
  onDocumentClick() {
    this.hideContextMenu();
  }
}
