import { Component, ElementRef, ViewChild } from '@angular/core';
import { BaModalComponent } from '../../../../theme/components/baModal/baModal.component';
import { Router, ActivatedRoute } from '@angular/router';
import { ScheduleInputService } from '../../scheduleInput.service';
import { Subscription } from "rxjs";
import { ServerDataSource } from '../../../../ng2-smart-table/ng2-smart-table';
import { Treatment, ScheduleInterface, Customer, Employee, Prescription, Disease, Medication } from '../../../../common/interfaces';
import 'style-loader!../../schedule.scss';
import { MasterRepo } from "../../../../common/repositories/masterRepo.service";
import { AuthService } from '../../../../common/services/permission';
import { CustomerSelect } from '../CustomerSelect/customerSelect.component';
@Component({
    selector: 'check-out',

    styleUrls: ['../../schedule.scss'],
    templateUrl: './CheckOut.component.html',

})
export class CheckOutEntry {
    @ViewChild('childModal') childModal: BaModalComponent;
    @ViewChild(CustomerSelect) CustomerSelect: CustomerSelect


    DialogMessage: string = "Saving data please wait ..."
    mode: string = "";
    modeTitle: string = '';
    public codeValue: string;
    newCurrentWeight: string;
    private returnUrl: string;
    private subcriptions: Array<Subscription> = [];
    user: any = {};
    SNO: number = 0;
    remarks: string;
    public model: any = {
        CUSTOMER : {},
        checkOutServices:[]
    };

    constructor(private router: Router, private masterService: MasterRepo, private activatedRoute: ActivatedRoute, private _authService: AuthService) {
        var setting = this._authService.getSetting();
        if (!!this.activatedRoute.snapshot.params['returnUrl']) {
            this.returnUrl = this.activatedRoute.snapshot.params['returnUrl'];
        }
        if (!!this.activatedRoute.snapshot.params['mode']) {
            this.mode = this.activatedRoute.snapshot.params['mode'];
        }
    }

    ngOnInit() {
        let sid = this.activatedRoute.snapshot.params['id'];
        if (this.mode != 'add') {
            this.masterService.postmaster(this.mode, { checkInId: sid }, "/GetDataForCheckOut").subscribe(data => {
                if (data.status == 'ok') {
                    if (data.result != null) {
                        this.model = data.result;
                        this.model.totalQty = this.model.Services.reduce((a,b)=>a + b.quantity, 0);
                        this.model.totAmnt = this.model.Services.reduce((a,b)=>a + b.amount, 0);
                        this.model.discount = this.model.Services.reduce((a,b)=>a + b.discount, 0);
                        this.model.vat = this.model.Services.reduce((a,b)=>a + b.vat, 0);
                        this.model.netAmnt = this.model.Services.reduce((a,b)=>a + b.netAmount, 0);
                        this.model.remarks = data.result.remarks;
                    }
                }
                else {
                    this.DialogMessage = data.result;
                    setTimeout(() => {
                        this.childModal.hide();
                    }, 3000)
                }
            },
                error => { alert(error) }
            );
        }
    }
    

    SaveClickEvent() {
        this.DialogMessage = "Saving Data please wait..."
        this.childModal.show();
        this.onsubmit();
    }

    saveChildModal(replacementEmp) {
        this.DialogMessage = "Updating Data please wait..."
        this.childModal.show();
    }

    hideChildModal() {
        this.childModal.hide();
    }

    onsubmit() {
        try {
            let sub = this.masterService.postmaster('add', this.model, "/saveCheckOut")
                .subscribe(data => {
                    if (data.status == 'ok') {
                        //Displaying dialog message for save with timer of 1 secs
                        this.DialogMessage = "Data Saved Successfully"
                        setTimeout(() => {
                            this.childModal.hide();
                            this.router.navigate([this.returnUrl]);
                        }, 1000)
                    }
                    else {
                        this.DialogMessage = data.result;
                        setTimeout(() => {
                            this.childModal.hide();
                        }, 3000)
                    }
                },
                    error => { alert(error) }
                );
            this.subcriptions.push(sub);
        }
        catch (e) {
            alert(e);
        }
    }
    onCancel() {
        this.router.navigate([this.returnUrl]);
    }
}
