import { Pipe, PipeTransform } from '@angular/core';

@Pipe({ name: 'searchMobile', pure: false })

export class MobilePipe implements PipeTransform {
  transform(data, query: string): any {
    if (data) {
      if (query.length) {
        return data.filter(item => {
          const searchedQuery = query.replace(/[^0-9]/ig, '').toLowerCase();
          const comId = (item.MOBILE) ? (item.MOBILE).replace(/[^0-9]/ig, '').toLowerCase() : '';
          if (
            comId.indexOf(searchedQuery) !== -1
          ) {
           
            return item;
          }
        });
      } else {
        
        return data;
      }
    }
  }
}