import { Component, ElementRef, ViewChild, AfterViewInit } from '@angular/core';
import { BaModalComponent } from '../../../../theme/components/baModal/baModal.component';
import { Router, ActivatedRoute } from '@angular/router';
import { ScheduleInputService } from '../../scheduleInput.service';
import { Subscription } from "rxjs";
import { Employee, ScheduleInterface, Service, Room, Customer, HolidayModel, Branch, ScheduleDetails, TransferModel } from '../../../../common/interfaces';
import 'style-loader!../../schedule.scss';
import { MasterRepo } from "../../../../common/repositories/masterRepo.service";
import { AuthService } from '../../../../common/services/permission';
import { DisableDateService } from '../../../../common/services/disable-date-picker/disable-date-picker.service';
import { v4 as uuidv4 } from 'uuid';
import { DatePipe } from '@angular/common';
import { partial } from 'lodash';
import { EmployeeTransfer } from '../EmployeeTransfer/employee-transfer.component';
@Component({
    selector: 'schedule',
    styleUrls: ['../../schedule.scss'],
    templateUrl: './schedule.html',
    providers: [DatePipe]
})
export class ScheduleInput implements AfterViewInit {
    @ViewChild('childEmployeeModal') childEmployeeModal: BaModalComponent;
    @ViewChild('childModal') childModal: BaModalComponent;
    @ViewChild('deleteModal') deleteModal: BaModalComponent;
    DialogMessage: string = "Saving data please wait ..."
    mode: string = "";
    modeTitle: string = '';
    private returnUrl: string;
    private subcriptions: Array<Subscription> = [];
    scheduleInput: ScheduleInterface = <ScheduleInterface>{};
    employeeBookedTimes: any = <any>{};
    roomBookedDetails: any = <any>{};
    user: any = {};
    SNO: number = 0;
    CustomerList: Customer[] = [];
    serviceList:Service[] = []; //Service List with Employee and Room
    ServiceList: Service[] = [];
    roomList: Room[] = [];
    employeeList: Employee[] = [];
    isServiceLoading: boolean = false;
    replacementEmployeeList: any[] = [];
    bookedEmployee: any = <any>{};
    multipleServiceList: ScheduleDetails[] = [];
    multipleServiceObj: ScheduleDetails =<ScheduleDetails>{};
    StartTime: any;
    EndTime: any;
    disableScheduleInput: boolean = false;
    IntegrateTreatmentWithAppointment: boolean;
    enableCheckIn:boolean = true;
    statusList: any[] = [];

    disabledDates: any[] = [];
    holiday: HolidayModel;
    formatDates: any[] = [];
    userBranches : Branch[] = [];

    ScheduleDateBS: string;
    appTimeSlots : any[] = [];
    empSetting = <any>{};
    serviceSetting = <any>{};
    roomSetting = <any>{};
    selectedService:Service[];
    transferList:TransferModel[]=[];

    constructor(
        private _scheduleInputService: ScheduleInputService,
        private router: Router,
        private masterService: MasterRepo,
        private elementRef: ElementRef,
        private activatedRoute: ActivatedRoute,
        private _authService: AuthService,
        private disableDateService: DisableDateService,
        private masterRepo: MasterRepo,
        private datePipe: DatePipe
    ) {

        const setting = this._authService.getSetting();
        this.IntegrateTreatmentWithAppointment = setting.IntegrateTreatmentWithAppointment;
        if (!!this.activatedRoute.snapshot.params['returnUrl']) {
            this.returnUrl = this.activatedRoute.snapshot.params['returnUrl'];
        }
        if (!!this.activatedRoute.snapshot.params['mode']) {
            this.mode = this.activatedRoute.snapshot.params['mode'];
        }
        this.empSetting = {
            enableCheckAll: true,
            text: 'Select Employees',
            enableSearchFilter: true,
            labelKey:"NAME",
            primaryKey:"EMPLOYEEID",
            disabled:true
        };
        this.roomSetting = {
            enableCheckAll: true,
            text: 'Select Rooms',
            enableSearchFilter: true,
            labelKey:"ROOMNO",
            primaryKey:"ROOMID",
            disabled:true
        };
        this.serviceSetting = {
            enableFilterSelectAll: false,
            text: 'Select Service',
            enableSearchFilter: true,
            labelKey:"DESCRIPTION",
            primaryKey:"SERVICEID",
            singleSelection:true,
            showCheckbox:false,
            disabled:true
        };
        this.multipleServiceObj.PAX = 1;
        this.paxChanged(1,1);
    }

    ngAfterViewInit() {
    }
    ngOnInit() {
        this.roomList = [];
        this.CustomerList = [];
        this.employeeList = [];
        this.ServiceList = [];
        
        let user = this._authService.getUserProfile();
        this.userBranches = user.branches;
        if(this.userBranches)
            this.scheduleInput.branch = this.userBranches.find(x=>x.branchId === user.defaultBranch);        
        this.getServices(user.defaultBranch);

        if (this.masterService._customerList.length > 0) { this.CustomerList = this.masterService._customerList; }
        else {
            this.masterService.getCustomerList().subscribe((data) => {
                this.CustomerList.push(<Customer>data);
            },
                Error => console.log(Error),
                () => {
                    this.masterService._customerList = this.CustomerList;
                });
        }
       
        this.user = this._scheduleInputService.getUser();

        if (this.mode == "edit") {
            let sid = this.activatedRoute.snapshot.params['id'];
            this.masterService.postmaster(this.mode, { sid: sid }, "/loadScheduleById").subscribe(data => {
                if (data.status == 'ok') {
                    this.scheduleInput = <ScheduleInterface>data.result;
                    this.scheduleInput.branch = this.userBranches.find(x=>x.branchId === this.scheduleInput.branch.branchId);  
                    this.scheduleInput.DATE = this.scheduleInput.DATE.toString().substring(0, 10);
                    this.multipleServiceList = this.scheduleInput.multipleServiceList;
                    this.getStatusList();
                    if (this.scheduleInput.billableSchedule == 1) {
                        this.disableScheduleInput = true;
                    }
                    else { this.disableScheduleInput = false; }
                    //  this.DialogMessage = "Data Loaded Successfully."
                    setTimeout(() => {
                        //    this.childModal.hide();

                    }, 1000)
                }
                else {
                    this.showMessage(data.result);
                }
            },
                error => { alert(error) }
            );
        } else {
            this._scheduleInputService.getGUID().subscribe((respose:any) => {
                this.scheduleInput.GUID = respose.guid;
            })
            this.scheduleInput.DATE = new Date().toLocaleDateString('en-CA');
            this.scheduleInput.STATUS = 0;
            this.changeEntryDate(this.scheduleInput.DATE, 'AD');
        }
    }

    ngOnDestroy() {
        this.subcriptions.forEach(sub => sub.unsubscribe());
        this.subcriptions = [];
        this.removeSchedule(null, false);
    }

    clearEmpRoom(){
        this.employeeList = [];
        this.roomList = [];
        this.multipleServiceObj.ROOM = undefined;
        this.multipleServiceObj.EMPLOYEE = undefined;
    }

    getServices(branchId :string){
        this.masterService.getServices(branchId)            
        .subscribe(services => {
            this.serviceList = services;
            this.ServiceList = services.map(service => new Service().getCoreObject(service));
            this.clearEmpRoom();
        });
    }

    getEmployees(branchId : string, service : Service, sEmp: Employee[] = null) {
        this.masterService.getEmployeeListNew(branchId)            
        .subscribe(response => {
            let branchEmp = response;
            let serviceEmp = this.serviceList.find(x => x.SERVICEID == service.SERVICEID).employees || [];
            if(this._authService.getSetting().MapEmpRoomToService)
                this.employeeList = branchEmp.filter(bEmp => serviceEmp.some(emp => bEmp.EMPLOYEEID == emp.id));
            else 
                this.employeeList = branchEmp;
            if (sEmp) {
                const emp = this.employeeList.filter(x => sEmp.some(emp => x.EMPLOYEEID == emp.EMPLOYEEID));
                if(emp.length<sEmp.length){
                    sEmp.forEach(x=>{
                        if(!this.employeeList.some(a=>a.EMPLOYEEID == x.EMPLOYEEID))
                            this.employeeList.push(x);
                    });
                }
                this.multipleServiceObj.EMPLOYEE = this.employeeList.filter(x => sEmp.some(emp => x.EMPLOYEEID == emp.EMPLOYEEID));
            }
        });
    }

    getRooms(branchId : string, service : Service, sRoom: Number[] = null){
        this.masterService.getRoomListNew(branchId)            
        .subscribe(response => {
            let branchRoom = response;
            let serviceRoom = this.serviceList.find(x => x.SERVICEID == service.SERVICEID).rooms || [];
            if(this._authService.getSetting().MapEmpRoomToService)
                this.roomList = branchRoom.filter(bRoom => serviceRoom.some(room => bRoom.ROOMID == room.id));
            else 
                this.roomList = branchRoom;
            if (sRoom) {
                this.multipleServiceObj.ROOM = this.roomList.filter(x => sRoom.some(room => x.ROOMID == room));
            }
        });
    }

    changeEntryDate(value, format: string) {
        if (format == "AD") {
            this.ScheduleDateBS = this.masterService.toBSDate(value)
        }

        else if (format == "BS") {
            this.scheduleInput.DATE = this.masterService.toADDate(value);
        }
        this.checkHoliday(this.scheduleInput.DATE, 0, "00:00", "00:00");
        this.enableServiceDetail();
    }

    SaveClickEvent() {
        this.scheduleInput.multipleServiceList = this.multipleServiceList;
        this.showMessage("Saving Data please wait.",-1);
        this.onsubmit();
    }

    starttimeChange(value: string) {
        if (value == null || value == "") return;
        let timeComponent = value.split(':');
        if (timeComponent) {
            let hour = parseInt(timeComponent[0]);
            let min = parseInt(timeComponent[1].substring(0, 2));
            let ampm = timeComponent[1].substring(3).trim().toLowerCase();

            if (ampm === "pm" && hour < 12) {
                hour += 12;
            } else if (ampm === "am" && hour === 12) {
                hour = 0; // Handle midnight
            }

            this.SetStartTime(hour, min);
            console.log(this.multipleServiceObj);

            if (this.multipleServiceObj.SERVICE != null) {
                // Calculate end time as service duration
                let endHour = hour;
                let endMin = min + this.multipleServiceObj.SERVICE.TIMEDURATION_MIN;

                // Handle minute overflow and midnight crossover
                while (endMin >= 60) {
                    endMin -= 60;
                    endHour += 1;
                }
                if (endHour >= 24) {
                    endHour -= 24;
                }

                this.SetEndTime(endHour, endMin);
            } else {
                this.SetEndTime(0, 0);
            }
        }
    }

    SetStartTime(hour, min) {
        this.StartTime = hour.toString().padStart(2, '0') + ":" + min.toString().padStart(2, '0')
        this.multipleServiceObj.STARTTIME = this.GetTimeString(hour, min);
    }

    SetEndTime(hour, min) {
        this.EndTime = hour.toString().padStart(2, '0') + ":" + min.toString().padStart(2, '0')
        this.multipleServiceObj.ENDTIME = this.GetTimeString(hour, min);
    }

GetTimeString(hour, min) {
    let ampm = " AM";
    let adjustedHour = hour;
    if (hour >= 12) {
        ampm = " PM";
        if (hour > 12) {
            adjustedHour = hour - 12;
        }
    } else if (hour === 0) {
        adjustedHour = 12; // Handle midnight
    }
    return adjustedHour.toString().padStart(2, '0') + ":" + min.toString().padStart(2, '0') + ampm;
}
    TypeChange(value) {
        console.log("type", value);
        this.multipleServiceObj.TYPE = value;
    }

    EmployeeChange(emp: Employee) {
        this.getTimeSlotForAppointment();
    }

    RoomChange(room: Room) {
        this.getTimeSlotForAppointment();
    }

    bookedEmployeeClickEvent(value) {
        if (value.EMPLOYEE_BOOKED_BYCUSTOMER == 1) return;
        this.bookedEmployee = value;
        this.replacementEmployeeList = [];
        this.childEmployeeModal.show();
    }
    saveChildModal(replacementEmp) {
        this.childEmployeeModal.hide();
        this.showMessage("Updating Data please wait.",-1);
        try {
            let sub = this.masterService.postmaster("addEdit", { replacementEmployee: replacementEmp, ToBeReplacedEmployee: this.bookedEmployee }, "/postReplacementEmployee")
                .subscribe(data => {
                    if (data.status == 'ok') {
                        this.showMessage("Data Exchanges Successfully",1000)
                    }
                    else {
                        this.showMessage(data.result)
                    }
                },
                    error => { alert(error) }
                );
            this.subcriptions.push(sub);
        }
        catch (e) {
            alert(e);
        }
    }

    hideChildModal() {
        this.childModal.hide();
    }

    onsubmit() {
        try {
            let body = this.scheduleInput;
            body.multipleServiceList.forEach(element => {
                element.SERVICE.rooms = [];
                element.SERVICE.employees = [];
                element.SERVICE.branches = [];
            });
            let sub = this.masterService.postmaster(this.mode, this.scheduleInput, "/saveSchedule")
                .subscribe(data => {
                    if (data.status == 'ok') {
                        this.showMessage("Data Saved Successfully.",1000);
                        this.router.navigate([this.returnUrl]);
                    }
                    else {
                        this.showMessage(data.result)
                    }
                },
                    error => { alert(error) }
                );
            this.subcriptions.push(sub);
        }
        catch (e) {
            alert(e);
        }
    }

    onCancel() {
        this.router.navigate([this.returnUrl]);
    }

    async AddClickEvent() {
        const serviceGuid = uuidv4();
        try {               
            var isEmpAvailable = true;
            (this.multipleServiceObj.EMPLOYEE as Employee[]).forEach(element => {
                if(element.OFFDAY.find(x => x == this.getWeekDay(new Date(this.scheduleInput.DATE))))
                    isEmpAvailable = false;
            });
            
            if (!isEmpAvailable) {
                if (this.user.Role == "admin") {
                    if (!confirm('One or more Employee will not be available on selected date. Do you want to add anyway?'))
                        return;
                }
                else {
                    this.showMessage('One or more Employee will not be available on selected date.');
                    return;
                }
            }
            if (this.StartTime === undefined) {
                this.showMessage('Please set Appointment Time first');
                return;
            }

            if (!(await this.checkHoliday(this.scheduleInput.DATE, this.multipleServiceObj.EMPLOYEE[0].EMPLOYEEID, this.multipleServiceObj.STARTTIME, this.multipleServiceObj.ENDTIME)))
                return;            

            this.showMessage("Validting Data please wait.",-1);

            
            const noOfService = this.multipleServiceObj.PAX * this.multipleServiceObj.SERVICE.noOfEmployees;
            const scheduleDateString = this.scheduleInput.DATE instanceof Date 
                ? this.scheduleInput.DATE.toISOString().split('T')[0] 
                : String(this.scheduleInput.DATE).substring(0, 10);
            
            for (let i =0; i < noOfService;i++) {
                const emp = this.multipleServiceObj.EMPLOYEE[i] || this.multipleServiceObj.EMPLOYEE[0];
                const room = this.multipleServiceObj.ROOM[i] || this.multipleServiceObj.ROOM[0];
                
                // Check if employee is in transfer list and validate time
                const transfer = this.transferList.find(x => x.employee.EMPLOYEEID === emp.EMPLOYEEID);
                if (transfer) {
                    // Check if transfer date matches schedule date
                    const transferDateString = String(transfer.date).substring(0, 10);
                    
                    if (transferDateString === scheduleDateString) {
                        // Validate if schedule time is within transfer time range
                        if (!this.isTimeWithinTransfer(
                            this.multipleServiceObj.STARTTIME, 
                            this.multipleServiceObj.ENDTIME, 
                            transfer.startTime, 
                            transfer.endTime
                        )) {
                            this.showMessage(`Schedule time must be within transfer time range (${transfer.startTime} - ${transfer.endTime}) for employee ${emp.NAME}.`);
                            return;
                        }
                    }
                }
                
                let data = {
                    Date: this.scheduleInput.DATE,
                    Service: this.multipleServiceObj.SERVICE,
                    CustomerId: this.scheduleInput.CUSTOMER.CUSID,
                    EmployeeId: emp.EMPLOYEEID,
                    RoomId: room.ROOMID,
                    STARTTIME: this.multipleServiceObj.STARTTIME,
                    ENDTIME: this.multipleServiceObj.ENDTIME,
                    StartTime: this.StartTime,
                    EndTime: this.EndTime,
                    GUID: this.scheduleInput.GUID,
                    SNO: this.SNO + 1,
                    ScheduleId: this.scheduleInput.ScheduleId,
                    serviceGuid,
                    branchId: this.scheduleInput.branch.branchId
                }
                let result = await this.masterService.postMasterAsync('validate', data, "/ValidateSchedule");                
            }

            
            for (let i =0; i < noOfService;i++) {
                const emp = this.multipleServiceObj.EMPLOYEE[i] || this.multipleServiceObj.EMPLOYEE[0];
                const room = this.multipleServiceObj.ROOM[i] || this.multipleServiceObj.ROOM[0];

                let isAddOn:boolean = i % this.multipleServiceObj.SERVICE.noOfEmployees !=0;
                this.multipleServiceList.push({...this.multipleServiceObj, SNO:++this.SNO, EMPLOYEE:emp, ROOM:room, isAddOn, serviceGuid});
            }
           
            setTimeout(() => {
                this.selectedService = <Service[]>[];
                this.multipleServiceObj = <any>{PAX:1};
                this.paxChanged(1,1);
                this.SetStartTime(0, 0);
                this.SetEndTime(0, 0);
                this.childModal.hide();
            }, 1000)
        }
        catch (e) {
            console.log("Validate Appointment Error :",e);
            this.hideChildModal();
            this.removeSchedule(serviceGuid, false);
        }
    }

    onSearchChange(searchValue: string) {
        var cus = this.CustomerList.find(x => x.MOBILE == searchValue);
        if (cus != null)
            this.scheduleInput.CUSTOMER = cus;
    }

    onBarcodeChange(searchValue: string) {
        var cus = this.CustomerList.find(x => x.BARCODE == searchValue);
        if (cus != null)
            this.scheduleInput.CUSTOMER = cus;
    }

    editSchedule(index) {
        try {
            console.log(this.multipleServiceList[index]);
            let selectedSchedule = this.multipleServiceList[index];
            this.selectedService = [selectedSchedule.SERVICE];
            this.multipleServiceObj =  { ...this.multipleServiceObj,
                SERVICE: this.ServiceList.find(x => x.SERVICEID == selectedSchedule.SERVICE.SERVICEID),
                SNO: selectedSchedule.SNO,
                CUSTOMERID: selectedSchedule.CUSTOMERID,
                STARTTIME: selectedSchedule.STARTTIME,
                ENDTIME: selectedSchedule.ENDTIME,
                EMPLOYEE_BOOKED_BYCUSTOMER: selectedSchedule.EMPLOYEE_BOOKED_BYCUSTOMER,
                RATE: selectedSchedule.RATE,
                TYPE: selectedSchedule.TYPE,
                PAX: selectedSchedule.PAX,
            };
            let empList = this.multipleServiceList
                              .filter(x=>x.guid == selectedSchedule.guid)
                              .map(x=>(<Employee>x.EMPLOYEE));
            console.log("empList", empList);
            this.paxChanged(this.multipleServiceObj.PAX, this.multipleServiceObj.SERVICE.noOfEmployees);
            this.getEmployees(this.scheduleInput.branch.branchId, this.multipleServiceObj.SERVICE, empList);
            this.getRooms(this.scheduleInput.branch.branchId, this.multipleServiceObj.SERVICE, [(selectedSchedule.ROOM as Room).ROOMID]);

            this.starttimeChange(selectedSchedule.STARTTIME);

            this.removeSchedule(selectedSchedule.serviceGuid, false);
            this.enableServiceDetail();
        }
        catch (e) {
            alert(e);
        }
    }

    removeSchedule(serviceGuid:string, showMessage:boolean = true) {
        const data = {
            GUID: this.scheduleInput.GUID,
            serviceGuid
        }
        if (showMessage)
        this.showMessage("Removing Appointment please wait.",-1);
        let sub = this.masterService.postmaster('remove_row', data, "/DeleteTempSchedule")
        .subscribe(data => {
            //console.log(data);
            if (data.status == 'ok') {
                this.multipleServiceList = this.multipleServiceList.filter(x=>x.serviceGuid != serviceGuid);
                if(showMessage)
                    this.showMessage("Success",1000);
            }
            else {
                if(showMessage)
                    this.showMessage(data.result);
            }
        });
    }    

    getWeekDay(date: Date): string {
        switch (date.getDay()) {
            case 0:
                return "sunday";
            case 1:
                return "monday";
            case 2:
                return "tuesday";
            case 3:
                return "wednesday";
            case 4:
                return "thursday";
            case 5:
                return "friday";
            case 6:
                return "saturday";

            default:
                return "saturday";
        }
    }
    
    serviceChangeEvent(e) {
        this.isServiceLoading = true;
        if(!this.scheduleInput.CUSTOMER) return;
        this.multipleServiceObj.SERVICE = e[0]
        this.multipleServiceObj.RATE = this.multipleServiceObj.SERVICE.RATE;

        this.clearEmpRoom();
        this.getEmployees( this.scheduleInput.branch.branchId, this.multipleServiceObj.SERVICE);
        this.getRooms( this.scheduleInput.branch.branchId, this.multipleServiceObj.SERVICE);
        this.getTimeSlotForAppointment();
        this.paxChanged(this.multipleServiceObj.PAX, e[0].noOfEmployees);
        this.isServiceLoading = false;
    }
    canBeBillable() {
        return (new Date() >= new Date(this.scheduleInput.DATE) && this._authService.getSetting().IntegratePos);
    }
    ProceedBill() {
        this.scheduleInput.billableSchedule = 1;
        this.SaveClickEvent();
    }
    CancelBill() {
        this.scheduleInput.multipleServiceList = this.multipleServiceList;
        this.showMessage("Saving Data please wait.",-1);
        try {
            let sub = this.masterService.postmaster(this.mode, this.scheduleInput, "/cancelBill")
                .subscribe(data => {
                    if (data.status == 'ok') {
                        this.showMessage("Data Saved Successfully", 1000);
                    }
                    else {
                        this.showMessage(data.result);
                    }
                },
                    error => { alert(error) }
                );
            this.subcriptions.push(sub);
        }
        catch (e) {
            alert(e);
        }
    }

    DeleteConformation() {
        this.deleteModal.show();
    }
    DeleteEvent() {
        this.deleteModal.hide();
        this.showMessage("Deleting this schedule please wait.",-1);
        let sub = this.masterService.postmaster('delete', this.scheduleInput, "/DeleteSchedule")
            .subscribe(data => {
                //console.log(data);
                if (data.status == 'ok') {
                    this.showMessage("Delete Successfull.", 1000)                    ;
                    this.router.navigate([this.returnUrl]);
                }
                else {
                    this.showMessage(data.result);
                }
            },
                error => { alert(error) }
            );
    }

    customerChanged(e) {
        this.scheduleInput.CUSTOMER = e;
    }

    async checkHoliday(date, employee, startTime, endTime) {
        var roleRes = this.masterService.getRole();
        var roleJSON = JSON.parse(roleRes);
        var res = await this._scheduleInputService.checkHoliday({ date, employee, startTime, endTime }).toPromise<any>();
        if (res.status == "ok" && res.result == true) {
            if (roleJSON.Role == "admin") {
                if (confirm("Staff is not available for selected Date/Time. Do you still want to add appointment?")){
                    this.scheduleInput.OverrideHoliday = true;
                    return true;
                }
                else return false;
            }
            else {
                this.showMessage("Staff is not available for selected Date/Time.",-1);
                return false;
            }
        }
        return true;
    }

    getStatusList() {
        this.statusList = [];
        console.log(this.scheduleInput.STATUS);
        switch (this.scheduleInput.STATUS) {
            case 0:
                this.statusList.push({ status: 1, description: "Patient Arrived" });
                break;
            case 1:
                this.statusList.push({ status: 0, description: "Initial" });
                this.statusList.push({ status: 2, description: "Patient In Session" });
                break;
            case 2:
                this.statusList.push({ status: 0, description: "Initial" });
                this.statusList.push({ status: 1, description: "Patient Arrived" });
                this.statusList.push({ status: 3, description: "Session Completed" });
                break;
            case 3:
                this.statusList.push({ status: 0, description: "Initial" });
                this.statusList.push({ status: 1, description: "Patient Arrived" });
                this.statusList.push({ status: 2, description: "Patient In Session" });
                break;
        }
    }

    getTimeSlotForAppointment(){
        let model = {
            customerId : this.scheduleInput.CUSTOMER.CUSID,
            outletId : this.scheduleInput.branch.branchId,
            serviceId : this.multipleServiceObj.SERVICE.SERVICEID,
            appointmentDate : this.scheduleInput.DATE,
            employeeId : 0,
            roomId :0
        }
        if(Array.isArray(this.multipleServiceObj.EMPLOYEE) && this.multipleServiceObj.EMPLOYEE.length == 1)
            model.employeeId = (this.multipleServiceObj.EMPLOYEE as Employee[])[0].EMPLOYEEID;
        else if(this.multipleServiceObj.EMPLOYEE)
            model.employeeId = (this.multipleServiceObj.EMPLOYEE as Employee).EMPLOYEEID;
        if(this.multipleServiceObj.ROOM)
            model.roomId = (this.multipleServiceObj.ROOM as Room).ROOMID;
        console.log(model);        
        this.masterService.getTimeSlotForAppointment(model)            
        .subscribe((response: any) => {
            this.appTimeSlots = <any[]>response.result;
        });
    }

    selectAppointmentTime(time){
        this.starttimeChange(time.time);
        if(!this.multipleServiceObj.EMPLOYEE)
            this.multipleServiceObj.EMPLOYEE = this.employeeList.filter(x=>x.EMPLOYEEID == time.employeeId);
        if(!this.multipleServiceObj.ROOM)
            this.multipleServiceObj.ROOM = this.roomList.filter(x=>x.ROOMID == time.roomId);
    }

    paxChanged(pax:number, noOfEmpRequred:number){
        const totalEmp:number = noOfEmpRequred * pax;
        this.empSetting = Object.assign({}, this.empSetting, {limitSelection:totalEmp});
        this.roomSetting = Object.assign({}, this.roomSetting, {limitSelection:pax});
    }

    showMessage(message:string, closeAfter = 3000){
        this.DialogMessage = message;
        if(!this.childModal.isShown)
            this.childModal.show();
        if(closeAfter>=0){
            setTimeout(() => {
                this.childModal.hide();
            }, closeAfter);
        }
    }

    enableServiceDetail(){
        this.serviceSetting = Object.assign({}, this.serviceSetting, {disabled:false});
        this.empSetting = Object.assign({}, this.empSetting, {disabled:false});
        this.roomSetting = Object.assign({}, this.roomSetting, {disabled:false});
    }

    // Convert time string (HH:MM AM/PM) to minutes since midnight
    private timeToMinutes(timeStr: string): number {
        if (!timeStr) return 0;
        const parts = timeStr.split(' ');
        const timePart = parts[0];
        const ampm = parts[1].toUpperCase();
        const [hours, minutes] = timePart.split(':').map(Number);
        let totalMinutes = hours * 60 + minutes;
        if (ampm === 'PM' && hours !== 12) {
            totalMinutes += 12 * 60;
        } else if (ampm === 'AM' && hours === 12) {
            totalMinutes -= 12 * 60;
        }
        return totalMinutes;
    }

    // Convert 24-hour time string (HH:MM) to minutes since midnight
    private time24ToMinutes(timeStr: string): number {
        if (!timeStr) return 0;
        const [hours, minutes] = timeStr.split(':').map(Number);
        return hours * 60 + minutes;
    }

    // Check if schedule time is within transfer time range
    private isTimeWithinTransfer(scheduleStartTime: string, scheduleEndTime: string, transferStartTime: string, transferEndTime: string): boolean {
        const scheduleStart = this.timeToMinutes(scheduleStartTime);
        const scheduleEnd = this.timeToMinutes(scheduleEndTime);
        const transferStart = this.time24ToMinutes(transferStartTime);
        const transferEnd = this.time24ToMinutes(transferEndTime);
        
        // Check if schedule time overlaps with transfer time
        return scheduleStart >= transferStart && scheduleEnd <= transferEnd;
    }

    addTransferEmp(){
        const dateString = this.scheduleInput.DATE instanceof Date 
            ? this.scheduleInput.DATE.toISOString().split('T')[0] 
            : String(this.scheduleInput.DATE);
        this.masterRepo.GetTransferEmp(this.scheduleInput.branch.branchId, dateString, this.selectedService[0].SERVICEID).subscribe(
            (data: TransferModel[]) => {
                let count: number = 0;
                data.forEach(emp => {
                    if (!this.employeeList.some(existingEmp => existingEmp.EMPLOYEEID === emp.employee.EMPLOYEEID)) {
                        this.employeeList.push(emp.employee);
                        this.transferList.push(emp);
                        count++;
                    }
                });
                if(count>0)
                    this.showMessage(`${count} employee added.`,1000);
                else 
                    this.showMessage(`Transferred employee not available.`,1000);
                console.log(count);
            },
                error => { alert(error) }
            );
    }
}
