import { Component, ViewChild } from '@angular/core';
import { AuthService } from "./../../../../common/services/permission/authService.service";
import { ModalDirective } from 'ngx-bootstrap'
import { LocalDataSource } from '../../../../ng2-smart-table/ng2-smart-table';
import 'style-loader!../../../masters/components/smartTables.scss';
import { MasterRepo } from "../../../../common/repositories/masterRepo.service";
import { Customer } from '../../../../common/interfaces'
import { Router } from '@angular/router';
import { Subscription } from "rxjs";

@Component({
    selector: 'followup',
    templateUrl: './followup.component.html',
    providers: [],
})
export class FollowUp {

    settings = {
        columns: {
            PatientName: {
                title: 'Patient',
                type: 'string',
                editable: false
            },
            Mobile: {
                title: 'Mobile',
                type: 'string',
                editable: false
            },
            Staff: {
                title: 'Staff',
                type: 'string'
            },
            TreatmentDate: {
                title: 'Treatment Date',
                type: 'date',
                editable: false
            },
            FollowupDate: {
                title: 'Follow-Up Date',
                type: 'string'
            },
            Time: {
                title: 'Time',
                type: 'string'                
            },
        }
    };

    data: any[] = [];
    private subcriptions: Array<Subscription> = [];
    source: LocalDataSource = new LocalDataSource();
    constructor(private service: MasterRepo, private _authService: AuthService, private router: Router) {
        this.source.load(this.data);
    }

    OnDateChangeEvent(v) {
        this.data = [];
        this.service.getFollowUpList(v).subscribe((data) => {
            this.data.push(data);
        },
            Error => console.log(Error),
            () => {
                //console.log(this.data)
                this.source.load(this.data);
            });
    }
    ngOnInit() {
        this.OnDateChangeEvent(new Date());
    }
}