import { Component, ViewChild } from '@angular/core';
import { AuthService } from "./../../../../common/services/permission/authService.service";
import { BaModalComponent } from '../../../../theme/components/baModal/baModal.component'
import { LocalDataSource } from '../../../../ng2-smart-table/ng2-smart-table';
import 'style-loader!../../../masters/components/smartTables.scss';
import { MasterRepo } from "../../../../common/repositories/masterRepo.service";
import { Branch } from "../../../../common/interfaces";
import { Router } from '@angular/router';
import { Subscription } from "rxjs";
import { DatePipe } from '@angular/common';
import { title } from 'process';
import { GlobalState } from '../../../../global.state';
import { LabelPipe } from '../../../../theme/pipes/label.pipe';
import { BranchSettingService } from '../../../../common/services/branch-setting.service';

@Component({
  selector: 'check-in-list',
  templateUrl: './CheckInList.component.html',
  providers: [],
})
export class CheckInList {
  query: string = '';
  @ViewChild('childModal') childModal: BaModalComponent;
  @ViewChild('deleteModal') deleteModal: BaModalComponent;
  DialogMessage: string = "You are not authorized";
  userBranches: Branch[] = [];
  selectedBranch: Branch[] = [];
  settings = {
    mode: 'external',
    delete: null,
    view: null,
    columns: {
      CUSTOMER: {
        title: this.state.getGlobalSetting("label-dictionary").filter(item => item.key === 'customer')[0].value,
        type: 'string',
        valuePrepareFunction: (CUSTOMER) => {
          return CUSTOMER.NAME;
        },
        filterFunction: (Patient, search: string) => {
          if (search.length > 0) {
            return Patient.NAME.toLowerCase().match(search.toLowerCase());
          }
          else {
            return Patient.NAME;
          }
        }

      },
      time :{
        title: "Checked-In On",
        type: 'string'
      },
      userId :{
        title: "Checked-In By",
        type: 'string'
      }
    }
  };
  private subcriptions: Array<Subscription> = [];
  source: LocalDataSource = new LocalDataSource();
  branchSetting: any; 

  constructor(
    private service: MasterRepo,
    private _authService: AuthService,
    private router: Router,
    private state: GlobalState,
    private labelPipe: LabelPipe,
    private branchSettingService: BranchSettingService
  ) {
    
    this.branchSetting = this.branchSettingService.getBranchSettings();
    
    this.userBranches = this._authService.getUserProfile().branches;
    this.selectedBranch.push(this.userBranches.find(x => x.branchId == this._authService.getUserProfile().defaultBranch));
    this.branchChanged();
  }

  branchChanged() {
    let Clist: Array<any> = [];
    this.service.GetAllCheckIns(this.selectedBranch.map(x => x.branchId))
      .subscribe(
        (data: any) => {
          Clist.push(...data);
        },
        (Error) => console.log(Error),
        () => {
          //console.log(Clist, "CLIST NG2")
          this.source.load(Clist);
          console.log(this.source);
        }
      );
  }

  onEditClick(event): void {
    this.router.navigate(["/pages/schedule/checkOut", { id: event.data.checkInId, mode: "edit", returnUrl: this.router.url }])
  }

}