export class Service {
    SERVICEID: number;
    MappingCode: string;
    DESCRIPTION: string;
    SkipTimeValidation: boolean;
    SkipTimeValidationSS: boolean;
    SkipTimeValidationRoom: boolean;
    SkipTimeValidationRoomSS: boolean;
    SkipTimeValidationCustomer: boolean;
    appointmentNotRequired: boolean;
    noOfEmployees: number;
    // TIMEDURATION_HRS:number;
    TIMEDURATION_MIN: number;
    RATE: number;
    branches:Branch[];
    employees:BaseModel<Number>[];    
    rooms:BaseModel<Number>[];

    public getCoreObject(service:Service):Service {
        return {
            SERVICEID: service.SERVICEID,
            DESCRIPTION: service.DESCRIPTION,
            TIMEDURATION_MIN: service.TIMEDURATION_MIN,
            RATE: service.RATE,
            MappingCode: service.MappingCode,
            SkipTimeValidation: service.SkipTimeValidation,
            SkipTimeValidationSS: service.SkipTimeValidationSS,
            SkipTimeValidationRoom: service.SkipTimeValidationRoom,
            SkipTimeValidationRoomSS: service.SkipTimeValidationRoomSS,
            SkipTimeValidationCustomer: service.SkipTimeValidationCustomer,
            appointmentNotRequired: service.appointmentNotRequired,
            noOfEmployees:service.noOfEmployees,
            branches: null,
            employees: null,
            rooms: null
        } as Service;
    }
}
