import { Component, ElementRef, ViewChild, AfterViewInit } from '@angular/core';
import { BaModalComponent } from '../../../../theme/components/baModal/baModal.component';
import { Router, ActivatedRoute } from '@angular/router';
import { ScheduleInputService } from '../../scheduleInput.service';
import { Employee, ScheduleInterface, Service, Room, Customer, HolidayModel, Branch } from '../../../../common/interfaces';
import 'style-loader!../../schedule.scss';
import { MasterRepo } from "../../../../common/repositories/masterRepo.service";
import { TimePickerComponent } from "angular2-timepicker/timepicker-component";
import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';
import { AuthService } from '../../../../common/services/permission';
import { settings } from 'cluster';
import { DefaultEditComponent } from '../../../../ng2-smart-table/src/ng2-smart-table/components/cell/cell-edit-mode';
import { DisableDateService } from '../../../../common/services/disable-date-picker/disable-date-picker.service';

import { DatePipe } from '@angular/common';
import internal, { strictEqual } from 'assert';
import { app } from 'electron';
import { gunzip } from 'zlib';
import { FreeTimeBlock } from '../../../../common/interfaces/freeTimeblock.interface';
import { TimeFormatService } from '../../../../common/services/time-format.service';
import { AppItem, AppRequest } from '../../../../common/interfaces/appoint-request.interface';
import { Outlet } from '../../../../common/interfaces/outlet.interface';
@Component({
  selector: "requestDetail",
  styleUrls: ["../../schedule.scss"],
  templateUrl: "./requestDetail.component.html",
  providers: [DatePipe],
})
export class AppointmentRequest implements AfterViewInit {
  @ViewChild("childModal") childModal: BaModalComponent;
  appRequest: AppRequest = <AppRequest>{};
  DialogMessage: string = "Saving data please wait ...";
  mode: string = "";
  modeTitle: string = "";
  private returnUrl: string;
  user: any = {};
  serviceList:Service[] = [];
  roomList: Room[] = [];
  cancelRemarks: string = '';
  employeeList: Employee[] = [];
  showCancelConfirmation: boolean = false;

  formatDates: any[] = [];
  userBranches: Branch[] = [];

  

  constructor(
    private router: Router,
    private masterService: MasterRepo,
    private scheduleService: ScheduleInputService,
    private activatedRoute: ActivatedRoute,
    private _authService: AuthService,
    private datePipe:DatePipe,
    private timeFormatService: TimeFormatService,
  ) {
    let today = new Date().toDateString();

    this.appRequest.outlet = <Outlet>{};
    this.appRequest.Customer = <Customer>{};
   
    const setting = this._authService.getSetting();
    if (!!this.activatedRoute.snapshot.params["returnUrl"]) {
      this.returnUrl = this.activatedRoute.snapshot.params["returnUrl"];
    } else this.returnUrl = "/pages/schedule/mastersche";
    if (!!this.activatedRoute.snapshot.params["mode"]) {
      this.mode = this.activatedRoute.snapshot.params["mode"];
    }
  }

  private get branchId():string | null {
    try {
      return this.appRequest.outlet.branchId ;  
    }catch (e) {
      console.error(e);
      return null;
    }
  }

  getServices(branchId: string) {
    this.masterService.getServices(branchId).subscribe(services => {
      this.serviceList = services;
      this.appRequest.items.forEach(element => {
        element.service = this.serviceList.filter(x=>x.SERVICEID == element.serviceId)[0] || <Service>{};
        element.empSetting = {
            enableCheckAll: true,
            text: 'Select Employees',
            enableSearchFilter: true,
            labelKey:"NAME",
            primaryKey:"EMPLOYEEID",
            limitSelection: element.quantity * element.service.noOfEmployees
          };
      });
    });
  }
  ngAfterViewInit() {
  }
  
  ngOnInit() {
    this.roomList = [];
    this.employeeList = [];
    let cartId = this.activatedRoute.snapshot.params['cartId'];
    this.getRequestDetail(cartId);

    let user = this._authService.getUserProfile();
    this.userBranches = user.branches;
  }

  getRequestDetail(cartId : string){
    this.masterService.GetAppointmetDetail(encodeURIComponent(cartId)).subscribe((data:any) => {
        this.appRequest = data;                   
        const formattedDate = this.datePipe.transform(data.appointmentDate, "yyyy-MM-dd");
        console.log(formattedDate);
        this.appRequest.miti =  this.masterService.toBSDate(formattedDate);
        this.appRequest.items.forEach(element => {           
          element.roomSetting = {
            enableCheckAll: true,
            text: 'Select Rooms',
            enableSearchFilter: true,
            labelKey:"ROOMNO",
            primaryKey:"ROOMID",
            limitSelection: element.quantity
          };
          this.getEmployees(this.branchId, this.appRequest.appointmentDate, element);
          this.getRooms(this.branchId, this.appRequest.appointmentDate, element);
        });  
        this.getServices(this.branchId);    
      },
      async (error:Response) => { 
        this.showMessage((await error.json()).result, 3000); 
        this.router.navigate([this.returnUrl]);       
      }
    );        
  }


  getEmployees(branchId: string, appDate:Date, service:any) {
    service.employeeList = [];
    this.masterService
      .getAvailableEmployees(branchId, service.serviceId, appDate, service.appointmentTime)
      .subscribe((response: any) => {
        service.employeeList.push(...response);
      });
  }
onCancelBooking() {
    this.showCancelConfirmation = true;
    this.cancelRemarks = ''; 
  }
  closeCancelPopup() {
    this.showCancelConfirmation = false;
    this.cancelRemarks = ''; 
  }  
  confirmCancel() {
    if (!this.cancelRemarks.trim()) {
      this.showMessage('Please provide a reason for cancellation...'); 
      return;
    }
    this.showMessage("Canceling Appointment Request please wait.",-1);
    this.cancelAppointment();
  }
  
  
  cancelAppointment() {   
    this.masterService.RejectAppointment(this.appRequest.cartId, this.cancelRemarks).subscribe(
      (response: any) => {
        if (response.status === 'ok') {
            this.showMessage("Request Cancelled Successfully.", 2000); 
            setTimeout(() => {              
              this.router.navigate([this.returnUrl]); 
            }, 1000);
        } else {
          this.showMessage(response.result, 3000); 
        }
      },
      (error: any) => {
        alert(error);
      }
    );
    this.showCancelConfirmation = false; 
    this.cancelRemarks = ''; 
  }

  getRooms(branchId: string, appDate:Date, service:any) {
    service.roomList = [];
    this.masterService
      .getAvailableRooms(branchId, service.serviceId, appDate, service.appointmentTime)
      .subscribe((response: any) => {
        service.roomList.push(...response)
      });
  }

  SaveClickEvent() {
    // Validate employee and room selections before proceeding
    for (const item of this.appRequest.items) {
      if ((!item.employee || item.employee.length === 0) || (!item.room || item.room.length === 0)) {
        this.showMessage("Please select  Therapist and room before proceeding...", 3000);
        return;
      }
    }
    this.DialogMessage = "Saving Data please wait...";
    this.childModal.show();

    this.onsubmit();
  }
  hideChildModal() {
    this.childModal.hide();
  }
  onsubmit() {
    try {
      const a = this.appRequest.items.map((item: AppItem,index) => Array(item.quantity)
        .fill(null)
        .map((_,i)=>({...item, index:i})))
        .reduce((acc, curr) => acc.concat(curr), []);
      const appointmentServices = a
        .map((s, index) => {          
          return {
            EMPLOYEE : s.employee[s.index] || s.employee[0],
            ROOM : s.room[s.index] || s.room[0],
            SERVICE : s.service,
            SNO : index,
            PAX : s.quantity,
            STARTTIME : s.appointmentTime,
          }
        });

      let body ={
          CUSTOMER : this.appRequest.Customer,
          branch : this.appRequest.outlet,
          DATE : this.appRequest.appointmentDate,
          GUID : this.appRequest.cartId,
          STATUS : 0,
          multipleServiceList : appointmentServices
      }
      this.masterService
        .postmaster("add", body, "/saveSchedule")
        .subscribe(
          (data) => {
              if (data.status == "ok") {
                this.masterService.ApproveAppointment(this.appRequest.cartId, data.result).subscribe((appResponse:any)=>{
                  if(appResponse.status == "ok"){
                    this.showMessage("Data Saved Successfully.",1000);   
                    this.router.navigate([this.returnUrl]);           
                  }
                  else {
                    this.showMessage(data.result)
                  }
                }, error=>alert(error));
            } else {
              this.showMessage(data.result);
            }
          },
          (error) => {
            alert(error);
          }
        );
    } catch (e) {
      alert(e);
    }
  }
  onCancel() {
    this.router.navigate([this.returnUrl]);
  }

  showMessage(message:string, closeAfter = 3000){
    this.DialogMessage = message;
    if(!this.childModal.isShown)
        this.childModal.show();
    if(closeAfter>=0){
        setTimeout(() => {
            this.childModal.hide();
        }, closeAfter);
    }
  }

  editTime(app:AppItem){
    this.scheduleService.getTimeSlotForService(this.appRequest.outlet.branchId, app.serviceId, this.appRequest.appointmentDate).subscribe((response: any) => {
      if(response.status == "ok"){
        app.freeBlocks = response.result.map((x:any) => <FreeTimeBlock>x);
        console.log('freeBlocks',app.freeBlocks);
      }
    });
    app.editTime = true;
  }

  onTimeBlockChange(app:AppItem){
    if(app.selectedTimeBlock && typeof app.selectedTimeBlock === 'object' && 'empList' in app.selectedTimeBlock && 'roomList' in app.selectedTimeBlock){
      const tBlock = app.selectedTimeBlock as FreeTimeBlock;
      app.employeeList = tBlock.empList;
      app.roomList = tBlock.roomList;
      app.displayTime = tBlock.Start;
      app.appointmentTime =  this.timeFormatService.to24HourFormat(app.displayTime);
      app.editTime = false;
    }
  }

  confirmTime(app:AppItem){
    if(app.selectedTimeBlock === "custom"){
      app.displayTime = this.timeFormatService.to12HourFormat(app.appointmentTime);
      this.getEmployees(this.branchId, this.appRequest.appointmentDate, app);
      this.getRooms(this.branchId, this.appRequest.appointmentDate, app);
    }
    app.editTime = false;
  }
}
