import { Component, ViewChild } from '@angular/core';
import { AuthService } from "./../../../../common/services/permission/authService.service";
import { BaModalComponent } from '../../../../theme/components/baModal/baModal.component'
import { LocalDataSource } from '../../../../ng2-smart-table/ng2-smart-table';
import 'style-loader!../../../masters/components/smartTables.scss';
import { MasterRepo } from "../../../../common/repositories/masterRepo.service";
import { Branch } from "../../../../common/interfaces";
import { Router } from '@angular/router';
import { Subscription } from "rxjs";
import { DatePipe } from '@angular/common';
import { title } from 'process';
import { GlobalState } from '../../../../global.state';
import { LabelPipe } from '../../../../theme/pipes/label.pipe';
import { BranchSettingService } from '../../../../common/services/branch-setting.service';

@Component({
  selector: 'transfer-list',
  templateUrl: './TransferList.component.html',
  providers: [],
})
export class TransferList {
  query: string = '';
  @ViewChild('childModal') childModal: BaModalComponent;
  @ViewChild('deleteModal') deleteModal: BaModalComponent;
  DialogMessage: string = "You are not authorized";
  userBranches: Branch[] = [];
  selectedBranch: Branch[] = [];
  settings = {
    mode: 'external',
    edit : null,
    delete : null,
    columns: {
      employee: {
        title: this.state.getGlobalSetting("label-dictionary").filter(item => item.key === 'employee')[0].value,
        type: 'string',
        valuePrepareFunction: (CUSTOMER) => {
          return CUSTOMER.NAME;
        },
        filterFunction: (Patient, search: string) => {
          if (search.length > 0) {
            return Patient.NAME.toLowerCase().match(search.toLowerCase());
          }
          else {
            return Patient.NAME;
          }
        }

      },
      branch:{
        title: "Transfer From",
        type: 'string',
        valuePrepareFunction: (branch:Branch) => {
          return branch.branchName;
        },
        filterFunction: (branch:Branch, search: string) => {
          if (search.length > 0) {
            return branch.branchName.toLowerCase().match(search.toLowerCase());
          }
          else {
            return branch.branchName;
          }
        }
      },
      transferTo:{
        title: "Transfer To",
        type: 'string',
        valuePrepareFunction: (branch:Branch) => {
          return branch.branchName;
        },
        filterFunction: (branch:Branch, search: string) => {
          if (search.length > 0) {
            return branch.branchName.toLowerCase().match(search.toLowerCase());
          }
          else {
            return branch.branchName;
          }
        }
      },
      date :{
        title: "Transfer Date",
        type: 'string'
      },
      startTime :{
        title: "Start Time",
        type: 'string'
      },
      endTime :{
        title: "End Time",
        type: 'string'
      },
      UserId :{
        title: "Transferred By",
        type: 'string'
      }
    }
  };
  private subcriptions: Array<Subscription> = [];
  source: LocalDataSource = new LocalDataSource();
  branchSetting: any; 

  constructor(
    private service: MasterRepo,
    private _authService: AuthService,
    private router: Router,
    private state: GlobalState,
    private labelPipe: LabelPipe,
    private branchSettingService: BranchSettingService
  ) {
    
    this.branchSetting = this.branchSettingService.getBranchSettings();
    
    this.userBranches = this._authService.getUserProfile().branches;
    this.selectedBranch.push(this.userBranches.find(x => x.branchId == this._authService.getUserProfile().defaultBranch));
    this.branchChanged();
    this.settings.delete = this.menuRight.delete ? {} : null;
      this.settings.edit = this.menuRight.edit ? {} : null;  
  }

  branchChanged() {
    let Clist: Array<any> = [];
    this.service.GetEmpTransferList(this.selectedBranch.map(x => x.branchId))
      .subscribe(
        (data: any) => {
          Clist.push(...data);
        },
        (Error) => console.log(Error),
        () => {
          this.service._transferList = Clist;
          this.source.load(Clist);
          console.log(this.source);
        }
      );
  }

  onAddClick(){
    this.router.navigate(['/pages/schedule/transfer', { mode: "add", returnUrl: this.router.url }])
  }

  get disableAdd(){
    return !this.menuRight.create;
  }
    
  get menuRight(){
    return this._authService.getRole.menuRights.find(x=>x.menuId === 'employee-transfer')
  }

  onEditClick(event): void {
    this.router.navigate(["/pages/schedule/transfer", { id: event.data.id, mode: "edit", returnUrl: this.router.url }])
  }
  
  onDeleteClick(event): void {
    this.selectedRow=event;
    this.deleteModal.show();
  }
  selectedRow:any;
  DeleteEvent(){
    this.deleteModal.hide();
    if (this._authService.getUserProfile() &&  this._authService.getUserProfile().Role=="admin") {
      this.DialogMessage = "Deleting please wait..."
      this.childModal.show();
      let sub = this.service.postmaster("delete", this.selectedRow.data, "/SaveTransfer")
        .subscribe(data => {
          if (data.status == 'ok') {
            //Displaying dialog message for save with timer of 1 secs
            if (this.service._transferList.length > 0) {
              this.service._transferList.splice(this.service._transferList.indexOf(this.selectedRow.data), 1);
              this.source.load(this.service._transferList);
            }
            this.DialogMessage = "Data Deleted Successfully"
            setTimeout(() => {
              this.childModal.hide();

            }, 1000)
          }
        });
      this.subcriptions.push(sub);
    }else{
      this.DialogMessage = "You are not authorized to delete  room."
      this.childModal.show();
    }
  }
}