import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { BaModalComponent } from 'src/app/theme/components/baModal/baModal.component';
import { Subscription } from 'rxjs';
import { Branch, Employee, HolidayModel, TransferModel } from '../../../../common/interfaces/master.interface';
import { MasterRepo } from '../../../../common/repositories';
import { DisableDateService } from '../../../../common/services/disable-date-picker/disable-date-picker.service';
import { DatePipe } from '@angular/common'
import * as multiselect from '../../../../../assets/js/multiselect-dropdown.js';
import { AuthService } from 'src/app/common/services/permission';
declare var MultiselectDropdown: any;
@Component({
    selector: 'employee-transfer',
    templateUrl: 'employee-transfer.component.html',
    providers: [DatePipe]
})

export class EmployeeTransfer implements OnInit {

    @ViewChild('childModal') childModal: BaModalComponent;
    DialogMessage: string = "Saving data please wait ..."

    employeeList: Employee[] = [];
    branchList: Branch[] = [];
    sEmp: Employee[];
    modeTitle: string = "";

    mode: string = "add";
    returnUrl: string;

    transfer: TransferModel = <TransferModel>{};

    valid: boolean = true;

    adisable: Date = new Date('2077-12-09');

    
    empSetting : any;

    private subscription: Array<Subscription> = [];

    constructor(private activatedRoute: ActivatedRoute, 
        private router: Router, 
        private masterRepo: MasterRepo,
        private disableDateService: DisableDateService, 
        private datePipe: DatePipe,
        private _authService:AuthService
    ) {

        if (!!this.activatedRoute.snapshot.params['returnUrl']) {
            this.returnUrl = this.activatedRoute.snapshot.params['returnUrl'];
        }
        if (!!this.activatedRoute.snapshot.params['mode']) {
            this.mode = this.activatedRoute.snapshot.params['mode'];
        }
    }

    options = {
        disableDaysAfter: 3,
        disableDaysBefore: 3
    }


    ngOnInit() {
        let user = this._authService.getUserProfile();
        this.masterRepo.getEmployeeListNew(user.defaultBranch)
            .subscribe((data : Employee[]) => {
                this.employeeList = data;
            },
            (err) => {
                console.log(err)
                this.masterRepo.handleWebError(err);
            },
            ()=>{
                this.getBranchList(user);
            }
        );
      
       
        this.empSetting ={
            enableFilterSelectAll: false,
            text: 'Select an Employee',
            enableSearchFilter: true,
            labelKey:"NAME",
            primaryKey:"EMPLOYEEID",
            singleSelection:true,
            showCheckbox:false,
        };
    }

    getBranchList(user:any){
        this.masterRepo.getBranchList().subscribe(
            (response: Branch[]) => {
                this.branchList = response.filter((x)=>x.branchId!=user.defaultBranch);
                this.transfer.branch = response.filter((x)=>x.branchId == user.defaultBranch)[0];
                console.log("gg");
            },
            (err) => {
                console.log(err)
                this.masterRepo.handleWebError(err);
            },
            ()=>{
                this.loadForm();
            }
        );
    }

    loadForm(){
        if (this.mode == "edit") {
            if (!!this.activatedRoute.snapshot.params['id']) {
                let id = this.activatedRoute.snapshot.params['id'];
                const t = this.masterRepo._transferList.filter(r => r.id == id)[0];
                this.transfer= {
                    id : t.id,
                    date : String(t.date).substring(0, 10),                    
                    branch: t.branch,
                    transferTo:this.branchList.find(x=>x.branchId == t.transferTo.branchId),
                    startTime : t.startTime,
                    endTime : t.endTime,
                    remarks : t.remarks,
                    employee : t.employee,
                    miti : this.masterRepo.toBSDate(String(t.date).substring(0, 10))

                };
                this.sEmp = this.employeeList.filter(x=>x.EMPLOYEEID == t.employee.EMPLOYEEID);
                console.log(this.transfer);
            }
        }
    }

    onSave() {
        this.DialogMessage = "Saving data please wait ...";
        this.childModal.show();
        this.onSubmit();
    }

    changeOnDate(value, format: string) {
        if (format == "AD") {
            this.transfer.miti = this.masterRepo.toBSDate(value);
        }
        else if (format == "BS") {
            this.transfer.date = this.masterRepo.toADDate(value);
        }
    }

    onSubmit() {

        try {
            if(!this.transfer.employee){
                this.DialogMessage = "Please select an employee.";
                this.childModal.show();
                return;
            }
            
            if(!this.transfer.transferTo){
                this.DialogMessage = "Please select an outlet.";
                this.childModal.show();
                return;
            }
            if(isNaN(Date.parse(this.transfer.date))){
                this.DialogMessage = "Please enter a valid date.";
                this.childModal.show();
                return;
            }
            if (!this.validateTime()) return;
            //console.log(this.holiday);
            if (this.valid == true) {
                //if (false) {

                let sub = this.masterRepo.postmaster(this.mode, this.transfer, "/SaveTransfer")
                    .subscribe(data => {
                        if (data.status == 'ok') {
                            //Displaying dialog message for save with timer of 1 secs
                            this.DialogMessage = "Data Saved Successfully"
                            setTimeout(() => {
                                this.childModal.hide();
                                this.router.navigate([this.returnUrl]);
                            }, 1000)
                        }
                        else {
                            this.DialogMessage = data.result;
                            setTimeout(() => {
                                this.childModal.hide();
                            }, 3000)
                        }
                    },
                        error => { alert(error) }
                    );

                () => {

                    //SEND TO NEPALIDATE PICKER FOR DISABLE
                    this.disableDateService.getDisableDates();

                    this.subscription.push(sub);
                }

            }
            else {
                this.DialogMessage = "Form is Invalid"
                this.childModal.show();
            }
        }

        catch (e) {
            console.log(e);
        }
    }


    hideChildModal() {
        this.childModal.hide();
    }


    onCancel() {
        this.router.navigate([this.returnUrl]);
    }

    validateTime(): boolean {
        if (!(this.transfer.startTime && this.transfer.endTime)) {
            this.DialogMessage = "Please select the time";
            this.childModal.show();
            return false
        }
        else if (this.transfer.startTime > this.transfer.endTime) {
            this.DialogMessage = "End time is earlier than Start Time";
            this.childModal.show();
            return false
        }
        return true;
    }
    
    empChanged(e){
        console.log(this.sEmp);
        this.transfer.employee = e;
    }
}
