import { Component, ViewChild, Input } from '@angular/core';
import { BaModalComponent } from '../../../../theme/components/baModal/baModal.component';
import { Router } from '@angular/router';
import { Customer } from '../../../../common/interfaces/master.interface';
import { ScheduleInputService } from '../../scheduleInput.service';
@Component({
    selector: 'customer-all-schedule',
    styleUrls: ['../../schedule.scss'],
    templateUrl: './customer-all-schedule.html'
})
export class CustomerAllSchedule {
    @ViewChild('CustomerSchedule') childCustomerScheduleModal: BaModalComponent;
    @Input() mobile: string = "";
    @Input() date: Date;
    schedules: any[] = [];
    Customer: Customer = <Customer>{};
    ShowAll:boolean = false;

    constructor(private router: Router, private scheduleService: ScheduleInputService) {
        this.schedules = [];
    }

    ngOnInit() {
        this.schedules = [];
    }

    showDialog() {
        this.Customer = <Customer>{}
        this.schedules = [];
        this.ShowAll = false;
        this.scheduleService.getCustomerSchedule({ FDate: this.date, CUSTOMER: this.mobile }).subscribe((response:any) => {
            if (response.customer != null) {
                this.schedules = response.schedule;
                this.Customer.NAME = response.customer.name;
                this.Customer.MOBILE = response.customer.mobile;
            }
            setTimeout(() => {
                this.childCustomerScheduleModal.show();
            }, 100);
        }
            , Error => {
                try {
                    let t = Error.json();
                    //console.log(t);
                    if (t.result == "Authorization Failed") {
                        this.router.navigate(['/login', this.router.url]);
                    }
                    else {
                        alert("Error in Loading Data:" + t.result);
                    }

                }
                catch (e) {
                    alert(e);
                }
            }
        );

    }

    labelClickEvent(id) {
        this.router.navigate(['/pages/schedule/scheInput', { mode: "edit", id: id, returnUrl: this.router.url }])
    }

    CheckAllChanged(e: boolean) {
        this.scheduleService.getCustomerSchedule({ FDate: (e ? new Date(2000, 1, 1) : this.date), CUSTOMER: this.mobile }).subscribe((response:any) => {
            if (response.customer != null) {
                this.schedules = response.schedule;
                this.Customer.NAME = response.customer.name;
                this.Customer.MOBILE = response.customer.mobile;
                console.log(this.Customer);
            }
        }
            , Error => {
                try {
                    let t = Error.json();
                    //console.log(t);
                    if (t.result == "Authorization Failed") {
                        this.router.navigate(['/login', this.router.url]);
                    }
                    else {
                        alert("Error in Loading Data:" + t.result);
                    }

                }
                catch (e) {
                    alert(e);
                }
            }
        );
    }

}