import { Component, ElementRef, ViewChild, AfterViewInit } from '@angular/core';
import { BaModalComponent } from '../../../../theme/components/baModal/baModal.component';
import { Router, ActivatedRoute } from '@angular/router';
import { ScheduleInputService } from '../../scheduleInput.service';
import { Subscription } from "rxjs";
import { Employee, ScheduleInterface, Service, Room, Customer, HolidayModel, Branch } from '../../../../common/interfaces';
import 'style-loader!../../schedule.scss';
import { MasterRepo } from "../../../../common/repositories/masterRepo.service";
import { TimePickerComponent } from "angular2-timepicker/timepicker-component";
import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';
import { AuthService } from '../../../../common/services/permission';
import { settings } from 'cluster';
import { DefaultEditComponent } from '../../../../ng2-smart-table/src/ng2-smart-table/components/cell/cell-edit-mode';
import { DisableDateService } from '../../../../common/services/disable-date-picker/disable-date-picker.service';

import { DatePipe } from '@angular/common';
import internal, { strictEqual } from 'assert';
import { KOTPrintComponent } from './kot-print.component';
import { AddOnService, AppointmentSummary, CheckInModel } from '../../../../common/interfaces/check-in.interface';
@Component({
  selector: "checkIn",
  styleUrls: ["../../schedule.scss"],
  templateUrl: "./checkIn.component.html",
  providers: [DatePipe],
})
export class CheckIn implements AfterViewInit {
  @ViewChild("childModal") childModal: BaModalComponent;
  //Check-In
  checkIn: CheckInModel = <CheckInModel>{};
  //Check-In
  DialogMessage: string = "Saving data please wait ...";
  mode: string = "";
  customerId: string = "";
  modeTitle: string = "";
  private returnUrl: string;
  private subcriptions: Array<Subscription> = [];
  user: any = {};
  SNO: number = 0;
  ServiceList: Service[] = [];
  roomList: any[] = [];
  employeeList: any[] = [];
  bookedEmployee: any = <any>{};

  formatDates: any[] = [];
  userBranches: Branch[] = [];

  addOnServices: AddOnService[] = <AddOnService[]>[];
  appointments: AppointmentSummary[] = <AppointmentSummary[]>[];
  serviceSetting: any = <any>{};

  public get enableCustomerSelect(): boolean {
    return this.customerId ? false : true;
  }

  constructor(
    private router: Router,
    private masterService: MasterRepo,
    private elementRef: ElementRef,
    private activatedRoute: ActivatedRoute,
    private _authService: AuthService,
    private disableDateService: DisableDateService,
    private masterRepo: MasterRepo,
    private datePipe: DatePipe,
    private printKot:KOTPrintComponent
  ) {
    let today = new Date().toDateString();
    var dayjs = require("../../../../../assets/js/dayjs.min.js");
    const formattedDate = dayjs(today).format("YYYY-MM-DD");

    this.checkIn.date = formattedDate;
    this.checkIn.miti = this.masterService.toBSDate(this.checkIn.date);

    const setting = this._authService.getSetting();
    if (!!this.activatedRoute.snapshot.params["returnUrl"]) {
      this.returnUrl = this.activatedRoute.snapshot.params["returnUrl"];
    } else this.returnUrl = "/pages/schedule/mastersche";

    if (!!this.activatedRoute.snapshot.params['customerId']) {
          this.customerId = this.activatedRoute.snapshot.params['customerId'];
      }
    this.router.events.subscribe((val) => {
      let data = {
        GUID: this.checkIn.GUID,
        SNO: 0,
      };
    });

    this.serviceSetting = {
      enableFilterSelectAll: false,
      text: "Select Service",
      enableSearchFilter: true,
      labelKey: "DESCRIPTION",
      primaryKey: "SERVICEID",
      singleSelection: true,
      showCheckbox: false,
    };
  }

  ngAfterViewInit() {
  }
  
  ngOnInit() {
    this.roomList = [];
    this.employeeList = [];
    this.ServiceList = [];
    let user = this._authService.getUserProfile();
    this.userBranches = user.branches;
    if (this.userBranches) {
      this.checkIn.branch = this.userBranches.find(
        (x) => x.branchId === user.defaultBranch
      );
    }
    this.getServices(user.defaultBranch);
    if(!!this.customerId){
      this.masterRepo.getCustomerById(this.customerId).subscribe(
        (customer: Customer) => {
          console.log(customer);
          this.checkIn.CUSTOMER = customer;
          this.GetAppointmentForCheckIn();
        }
      );
    }
  }

  getServices(branchId: string) {
    this.masterService.getServices(branchId).subscribe(services => {
      this.ServiceList = services;
    });
  }

  getEmployees(branchId: string, service, index: number) {
    this.masterService
      .getEmployeeListNew(branchId)
      .subscribe(branchEmp => {
        let serviceEmp = service.employees;
        this.addOnServices[index].employeeList = branchEmp.filter((bEmp) =>
          serviceEmp.some((emp) => bEmp.EMPLOYEEID == emp.id)
        );
      });
  }

  getRooms(branchId: string, service, index: number) {
    this.masterService.getRoomListNew(branchId).subscribe(branchRoom => {
      let serviceRoom = service.rooms;
      this.addOnServices[index].roomList = branchRoom.filter((bRoom) =>
        serviceRoom.some((room) => bRoom.ROOMID == room.id)
      );
    });
  }

  SaveClickEvent() {
    this.DialogMessage = "Saving Data please wait...";
    this.childModal.show();

    this.onsubmit();
  }
  onsubmit() {
    try {
      let addOns: any[] = [];
      addOns = this.addOnServices.map((x) => ({
        service: { id: x.service.SERVICEID, name: x.service.DESCRIPTION },
        room: x.room ? { id: x.room.ROOMID, name: x.room.ROOMNO } : null,
        employee: x.employee
          ? { id: x.employee.EMPLOYEEID, name: x.employee.NAME }
          : null,
      }));
      this.checkIn.Services = [...this.appointments.filter(x=>x.isChecked), ...addOns];
      if(this.checkIn.Services.length == 0){
        this.DialogMessage = "Please select at least one service.";
        this.childModal.show();
        return;
      }
      let sub = this.masterService
        .postmaster(this.mode, this.checkIn, "/saveCheckIn")
        .subscribe(
          (data) => {
            if (data.status == "ok") {
              //Displaying dialog message for save with timer of 1 secs
              this.DialogMessage = "Data Saved Successfully";
              this.print();
              setTimeout(() => {
                this.childModal.hide();
                this.router.navigate([this.returnUrl]);
              }, 1000);
            } else {
              this.DialogMessage = data.result;
              setTimeout(() => {
                  this.childModal.hide();
              }, 3000)
            }
          },
          (error) => {
            alert(error);
          }
        );
      this.subcriptions.push(sub);
    } catch (e) {
      alert(e);
    }
  }
  onCancel() {
    this.router.navigate([this.returnUrl]);
  }

  customerChanged(e) {
    this.checkIn.CUSTOMER = e;
    this.GetAppointmentForCheckIn();
  }

  GetAppointmentForCheckIn() {
    if(!this.checkIn.CUSTOMER) return;
    let model = {
      CUSID: this.checkIn.CUSTOMER.CUSID,
      outletId: this.checkIn.branch.branchId,
      FDate: this.checkIn.date,
    };
    this.masterService.GetAppointmentForCheckIn(model).subscribe(
      (response: any) => {
        this.appointments = response.result;
        if(response.status == "edit"){
          this.mode = "edit";
          this.checkIn.checkInId = response.RefNo;
          this.appointments.forEach((appointment) => appointment.isChecked = true);
        }
      },
      (error) => {
        let response = JSON.parse(error._body);
        this.DialogMessage = response.result;
        this.childModal.show();
        setTimeout(() => {
          this.checkIn.CUSTOMER = null;
          this.childModal.hide();
        }, 3000);
      }
    );
  }

  addNewRow() {
    this.addOnServices.push(<AddOnService>{});
  }

  serviceChangeEvent(e,index) {
    this.addOnServices[index].service = e[0];
    let branchId = this.checkIn.branch.branchId;
    this.getEmployees(branchId, e[0], index);
    this.getRooms(branchId, e[0], index);
  }

   print(){
      console.log(this.checkIn);
      setTimeout(() => {
        this.printKot.printKOT(
          this.checkIn.CUSTOMER,
          this.checkIn.Services
        );
      }, 1500);
  }  
}
