import { Component, Injectable, OnInit } from "@angular/core";
import { environment } from "src/environments/environment";
import {
  BaseModel,
  Customer,
  Employee,
  Service,
} from "../../../../common/interfaces";
import { LabelPipe } from "../../../../theme/pipes/label.pipe";
import { AppointmentSummary } from "../../../../common/interfaces/check-in.interface";

@Component({
  selector: "kot-print",
  template: `<div id="print"></div>`,
})
@Injectable()
export class KOTPrintComponent {
  today = new Date();
  date =
    this.today.getFullYear() +
    "-" +
    (this.today.getMonth() + 1) +
    "-" +
    this.today.getDate();
  time =
    this.today.getHours() +
    ":" +
    this.today.getMinutes() +
    ":" +
    this.today.getSeconds();
  dateTime = this.date + " " + this.time;

  constructor(private labelPipe: LabelPipe) {}

  printKOT(customer: Customer, services: AppointmentSummary[]) {
    console.log("@@printTop");

    let popupWin;
    let tableData = ``;
    services.forEach((service) => {
      tableData  += `<table style='font-size: 11px;font-family: Arial, Helvetica, sans-serif;
              border-collapse: collapse;border-top: none;border-bottom: none;'>`;
      let head = this.head(customer, service.service.name, service.employee.name, service.room.id.toString(), service.startTime);

      tableData += head + `</table>`;
    });
    popupWin = window.open("", "_blank", "top=0,left=0,height=50%,width=50%");
    if (
      popupWin.outerWidth < screen.availWidth ||
      popupWin.outerHeight < screen.availHeight
    ) {
      popupWin.moveTo(0, 0);
      popupWin.resizeTo(screen.availWidth, screen.availHeight);
    }
    popupWin.document.write(`<html> <head><title></title>
          <style>
          @media print
          {
            @page {
                size: 80mm auto; /* Width x Height */
                margin: 3mm; /* Adjust margins */
             }
            table { 
                page-break-after:always;
                font-family:Arial,sans-serif;
            }
                
          } 
          </style></head>
      <body style='width: 300px;heignt:500px;' onload="window.print();window.close()">
      ${tableData}
      </body>
      </html>`);
    popupWin.document.close();
  }

  head(
    customer: Customer,
    service: string,
    employee: string,
    room: string,
    serviceTime: string
  ): string {
    var header = "";
    const customerLabel = this.labelPipe.transform("customer");
    const employeeLabel = this.labelPipe.transform("employee");
    //Reset arrayIndex for multiple calls

    header =
      header +
      `
        <div class="row">
            <h4 style="font-family: Arial, Helvetica, sans-serif;text-align:center">Service Order Ticket</h4>
        </div>
        <div class="row">
            <p>Date: ${this.date}</p>
        </div>
        <div class="row">
            <p>${customerLabel}: ${customer.NAME}</p>
        </div>
        <div class="row">
            <p>Mobile : ${customer.MOBILE}</p>
        </div>
        <div class="row">
            <p>Service : ${service}</p>
        </div>
        <div class="row">
            <p>Time : ${serviceTime}</p>
        </div>
         <div class="row">
            <p>Time : ${room}</p>
        </div>
        <div class="row">
            <p>${employeeLabel} : ${employee}</p>
        </div>
        <hr>
        <style>
            p{
                font-size: 11px;
                font-family: Arial, Helvetica, sans-serif;
            }
        </style>
  `;
    return header;
  }
}
