import { Routes, RouterModule } from '@angular/router';
import { CanActivateTeam } from '../../common/services/permission/guard.service';
import { Schedule } from './schedule.component';
import { ModuleWithProviders } from '@angular/core';
import { MasterSchedule } from './components/ScheduleInput/masterSchedule.component';
import { ScheduleInput } from './components/ScheduleInput/scheduleInput.component';
import { TreatmentEntry } from './components/TreatmentEntry/Treatment.component';
import { FollowUp } from './components/FollowUp/followup.component'
import { TreatmentList } from './components/TreatmentEntry/TreatmentList.component';
import { TreatmentDetails} from './components/TreatmentEntry/TreatmentDetails.component';
import { CheckIn } from './components/CheckIn/checkIn.component';
import { CheckInList } from './components/CheckOut/CheckInList.component';
import { CheckOutEntry } from './components/CheckOut/CheckOut.component';
import { AppointmentRequestList } from './components/AppointmentRequest/RequestList.component';
import { AppointmentRequest } from './components/AppointmentRequest/requestDetail.component';
import { TransferList } from './components/EmployeeTransfer/TransferList.component';
import { EmployeeTransfer } from './components/EmployeeTransfer/employee-transfer.component';
// noinspection TypeScriptValidateTypes
export const routes: Routes = [
  {
    path: '',
    component: Schedule,
    children: [
      { path: 'mastersche', component: MasterSchedule, canActivate: [CanActivateTeam] },
      { path: 'scheInput', component: ScheduleInput, canActivate: [CanActivateTeam] },
      { path: 'checkIn', component: CheckIn, canActivate: [CanActivateTeam] },
      { path: 'check-in-list', component: CheckInList, canActivate: [CanActivateTeam] },
      { path: 'checkOut', component: CheckOutEntry, canActivate: [CanActivateTeam] },
      { path: 'treatment', component: TreatmentEntry, canActivate: [CanActivateTeam] },
      { path: 'followup', component: FollowUp, canActivate: [CanActivateTeam] },
      { path: 'treatment-list', component: TreatmentList, canActivate: [CanActivateTeam] },
      { path: 'treatment-details', component: TreatmentDetails, canActivate: [CanActivateTeam] },
      { path: 'appointment-requests', component: AppointmentRequestList, canActivate:[CanActivateTeam] },
      { path: 'request-detail', component: AppointmentRequest, canActivate:[CanActivateTeam] },
      { path: 'employee-transfer', component: TransferList, canActivate:[CanActivateTeam] },
      { path: 'transfer', component: EmployeeTransfer, canActivate:[CanActivateTeam] },
      // { path: 'schedule/scheInput', component: Schedule },
    ]
  },

];

export const routing: ModuleWithProviders = RouterModule.forChild(routes);
