import { Component, ViewChild, Output, EventEmitter, Input, OnChanges, SimpleChanges } from '@angular/core';
import { BaModalComponent } from '../../../../theme/components/baModal/baModal.component';
import { Router } from '@angular/router';
import { Customer } from '../../../../common/interfaces/master.interface';
import { MasterRepo } from "../../../../common/repositories/masterRepo.service";
import { AuthService } from '../../../../common/services/permission';
import { Subscription } from "rxjs";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { CommonService } from '../../../../common/services/common.service';
@Component({
  selector: 'customer-select',
  styleUrls: ['../../schedule.scss'],
  templateUrl: './CustomerSelect.html'
})
export class CustomerSelect implements OnChanges {
  @ViewChild('childModal') childModal: BaModalComponent;
  DialogMessage: string = "Saving data please wait ..."
  
  private subcriptions: Array<Subscription> = [];
  EnableCustomerEntryInAppointment: boolean;
  CustomerList: Customer[] = [];
  isLoading: boolean = false;
  mobileNo: string = '';
  name: string = '';
  SelectedCustomer: Customer = <Customer>{};
  @Input() disabled: boolean = false;
  @Output() customerChanged = new EventEmitter<Customer>();
  @Output() mobileChanged = new EventEmitter<Customer>();
  @Output() ncw = new EventEmitter();
  @Output() historyChange = new EventEmitter();
  CustomerEntryVisible: boolean;
  customerForm: FormGroup;
  listCustomer: string;
  CustomerInput: string;
  MobileInput: string;
  timeout: any = null;
  isSubmitting: boolean = false;
  activeSearchType: 'customer' | 'mobile' | null = null; 
  constructor(
    private router: Router,
    private masterService: MasterRepo,
    private _authService: AuthService,
    private fb: FormBuilder,
    private common: CommonService
  ) {
    const setting = this._authService.getSetting();
    this.EnableCustomerEntryInAppointment = setting.EnableCustomerEntryInAppointment;
  }

  ngOnInit() {
    this.CustomerList = [];
    this.customerForm = this.fb.group({
      CUSID: [''],
      NAME: [''],
      ADDRESS: [''],
      TELNO: [''],
      MOBILE: [''],
      IMOBILE: [''],
      PANNO: [''],
      EMAIL: [''],
      DOB: [''],
      Age: [''],
      PATIENT_ID: [''],
      Patient_Weight: [''],
      MSTATUS: [''],
      GENDER: [''],
      OCCUPATION: [''],
      EMERGENCYCONTACTNAME: [''],
      EMERGENCYCONTACTNO: [''],
      REGDATE: [''],
      BARCODE: [''],
      Membership: ['']
    });

    this.masterService.getFormPreferences("CustomerEntryCompact").subscribe((result: any) => {
      this.common.setFormPreference(this.customerForm, result.fields);
    });
    this.updateDisabledState();
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes['disabled']) {
      this.updateDisabledState();
    }
  }

  private updateDisabledState() {
    if (this.disabled) {
      if (this.customerForm) {
        this.customerForm.disable();
      }
    } else {
      if (this.customerForm) {
        this.customerForm.enable();
      }
    }
  }

  getCustomerList(name: string = '', mobile: string = '') {
    // Prevent new API call if already loading or disabled
    if (this.isLoading || this.disabled) return;

    const pageNumber = 1;
    const rowCount = 10;
    this.CustomerList = [];
    this.isLoading = true;

    const sub = this.masterService.getPagedCustomerList(pageNumber, rowCount, name, mobile).subscribe(
      (data: any) => {
        const customers: Customer[] = data.result;
        this.CustomerList.push(...customers);
        this.isLoading = false;
        this.masterService._customerList = this.CustomerList;
        this.activeSearchType = null; 
      },
      (error) => {
        this.isLoading = false;
        this.activeSearchType = null;
        this.masterService.handleWebError(error);
      }
    );
    this.subcriptions.push(sub);
  }

  searchKeyup(event: any, searchType: 'customer' | 'mobile') {
    if (!event.keyCode || this.disabled) return;

    clearTimeout(this.timeout);

    const searchTerm = event.target.value.trim();
    const $this = this;

    if (searchType === 'customer') {
      this.MobileInput = '';
    } else {
      this.CustomerInput = '';
    }

    if (searchTerm.length >= 1) {
      this.activeSearchType = searchType; 
      this.timeout = setTimeout(() => {
        // Only proceed if the current search type is still active
        if ($this.activeSearchType === searchType) {
          const name = searchType === 'customer' ? searchTerm : '';
          const mobile = searchType === 'mobile' ? searchTerm : '';
          $this.getCustomerList(name, mobile);
        }
      }, 500);
    } else {
      this.CustomerList = [];
      this.isLoading = false;
      this.activeSearchType = null;
    }
  }

  customer_keyup(event: any) {
    this.searchKeyup(event, 'customer');
  }

  mobile_keyup(event: any) {
    this.searchKeyup(event, 'mobile');
  }

  customerChange(event) {
    if (this.disabled) return;
    
    var customer = this.CustomerList.filter(x => event.target.value == x.NAME)[0];
    if (customer) {
      this.MobileInput = customer.MOBILE;
      this.SelectedCustomer = customer;
      this.customerChanged.emit(customer);
    }
  }

  mobileChange(event) {
    if (this.disabled) return;
    
    var customer = this.CustomerList.filter(x => event.target.value == x.MOBILE)[0];
    if (customer) {
      this.CustomerInput = customer.NAME;
      this.SelectedCustomer = customer;
      this.customerChanged.emit(customer);
    }
  }

  onAddNewCustomer() {
    if (this.disabled) return;
    
    if (this.EnableCustomerEntryInAppointment) {
      this.CustomerEntryVisible = true;
    } else {
      this.router.navigate(['/pages/masters/cus/detail-cus', { mode: "add", returnUrl: "/pages/schedule/scheInput" }])
    }
  }

  redirectToPatient() {
    if (this.disabled) return;
    
    if (this.CustomerEntryVisible) {
      this.CustomerEntryVisible = false;
    }
  }

  saveCustomer() {
    try {
      if (!this.customerForm.valid) {
        this.isSubmitting = true;
        return;
      }
      this.DialogMessage = "Saving Data please wait...";
      this.childModal.show();
      let cus: any = this.customerForm.value;
      //console.log("CUSTOMER DETAILS", cus);
      let sub = this.masterService.postmaster("add", cus, "/saveCustomer")
        .subscribe(data => {
            if (data.status == 'ok') {
              //Displaying dialog message for save with timer of 1 secs
              this.DialogMessage = "Data Saved Successfully"
              this.masterService.refreshCustomerList();
              this.ncw.emit(this.common.getFormValue(this.customerForm,"Patient_Weight"));
              cus.CUSID = data.result.cusid;
              this.customerChanged.emit(cus);
              const __this = this;
              this.CustomerInput = data.result.name;
              this.MobileInput = data.result.mobile;
              setTimeout(() => {
                __this.childModal.hide();
                __this.CustomerEntryVisible = false;
                __this.mobileNo = this.common.getFormValue(this.customerForm,"MOBILE");
                __this.SelectedCustomer = __this.SelectedCustomer;
                __this.CustomerList = __this.masterService._customerList;
                __this.onSearchChange(__this.mobileNo);
                this.isSubmitting = false;
              }, 1000);
            } else {
              this.DialogMessage = data.result;
              setTimeout(() => {
                  this.childModal.hide();
              }, 3000)
            }
          },
          (error) => {
            alert(error);
          }
        );
      this.subcriptions.push(sub);
    } catch (e) {
      alert(e);
    }
  }

  changeDOB(event) {
    var dob = event.target.value;
    let birthDate = new Date(dob);
    //console.log("NEW CUSTOMER DOB", this.newcustomerDOB);
    let timeDiff = Math.abs(Date.now() - birthDate.getTime());
    let age = Math.floor(timeDiff / (1000 * 3600 * 24) / 365.25);
    //console.log('AGE',age)
    this.customerForm.get("AGE").setValue(age);
  }

  onMobileChange(e) {
    if (e.target.value.length >= 10) {
      e.target.value = e.target.value.substring(0, 10);
    }
  }

  onSearchChange(searchValue: string) {
    var cus: any = this.CustomerList.find(x => x.MOBILE == searchValue);
    //console.log("cus",cus);
    if (cus != null) {
      this.SelectedCustomer = cus;
      this.mobileNo = cus.MOBILE;
      this.customerChanged.emit(cus);
    }
    //console.log(this.SelectedCustomer)
  }

  onBarcodeChange(searchValue: string) {
    if (this.disabled) return;
    
    var cus = this.CustomerList.find(x => x.BARCODE == searchValue);
    //console.log("barcode",cus);
    if (cus != null) {
      this.SelectedCustomer = cus;
      this.customerChanged.emit(cus);
    }
  }

  selectedCustomerChanged(e) {
    this.customerChanged.emit(this.SelectedCustomer);
    this.mobileNo = this.SelectedCustomer.MOBILE;
  }

  selectCustomer(event) {
    //console.log('EVENT', event);
  } 
}
